<template>
	<view>
		<view class="bg_img">
			<image style="height:630px;width:100%;" :src="img" mode=""></image>
		</view>
		<view>
			<img :src="src == '' ? '' : src" class="user_img" @click="src_img" />
		</view>
		<view class="reg-info">
			<input type="text" placeholder="用户名" @input="Username"/>
			<input type="text" placeholder="手机号" @input="iphone"/>
			<input type="text" placeholder="验证码" @input="Ver">
			<button id="btn" class="verify" @click="Verify">验证码</button>
			<view class="btn_login">
				<image style="height:45px;width:60%;margin:0 auto;display: block;" :src="button_img"></image>
				<span class="user_login">注册</span>
			</view>
		</view>
		{{code}}
	</view>
</template>

<script>
	export default {
		data() {
			return {
				src: '/static/image/qq.png',
				img: '/static/image/bg.png',
				button_img: '/static/image/button.png',
				time:null,
				//用户名
				username:'',
				//手机号
				number:'',
				//输入的验证码
				verify:'',
				//返回的验证码
				code:''
			};
		},
		methods:{
			//用户名
			Username(e) {
				this.username = e.detail.value;
			},
			//手机号
			iphone(e) {
				this.number = e.detail.value;
			},
			//验证码
			Ver(e) {
				this.verify = e.detail.value;
			},
			//发送验证码
			Verify() {
				var number = this.number;
				uni.request({
					url:"http://47.100.211.9:80/API/duanxin_API/industrySMS.php?iphone="+number,
					success(res) {
						this.code = res.data.note;
					}
				})
			}
		}
	}
</script>

<style>
	.bg_img {
		justify-content: center;
	}
	.user_img {
		width: 150upx;
		height: 150upx;
		border-radius: 50%;
		background: #8F8F94;
		position: fixed;
		top: 270upx;
		left: 0;
		right: 0;
		margin-left: auto;
		margin-right: auto;
	}
	.reg-info {
		position: fixed;
		top: 440upx;
		width: 100%;
	}
	
	input {
		border-bottom: 1px solid gray;
		width: 75%;
		margin: 0 auto;
		margin-top: 50upx;
		font-size: 30upx;
	}
	
	.btn_login {
		position: fixed;
		top: 840upx;
		width: 100%;
		margin: 0 auto;
	}
	.user_login {
		border: none;
		color: black;
		font-size: 30upx;
		background-size: cover;
		display: block;
		text-align: center;
		margin-top: -34px;
		font-size: 35upx;
		color: white;
	}
	.verify{
		width:160upx;
		height:60upx;
		position:fixed;
		top:670upx;
		left:500upx;
		font-size: 25upx;
	}
</style>



